// cieka: wwwroot/js/components/userLocationMap.js
import React, { useState, useMemo, useEffect } from 'react';
import LocationPicker from './locationPicker.js';

const UserLocationMap = ({ address, userSearchRadius, userId, isCurrentUser = false }) => {
    console.log('[UserLocationMap] Props otrzymane:', { address, userSearchRadius, userId, isCurrentUser });

    const [isEditing, setIsEditing] = useState(false);

    // Sprawd, czy props.address jest zdefiniowane i czy zawiera liczbowe wsprzdne
    const hasCoordinates = useMemo(() => {
        const result = address && typeof address.latitude === 'number' && typeof address.longitude === 'number';
        console.log('[UserLocationMap] Obliczanie hasCoordinates. Adres prop:', address, 'Wynik:', result);
        return result;
    }, [address]);

    const initialLocationForPicker = useMemo(() => {
        console.log('[UserLocationMap] useMemo dla initialLocationForPicker. Adres:', address, 'UserSearchRadius:', userSearchRadius, 'HasCoordinates:', hasCoordinates);
        let computedLoc;
        if (hasCoordinates) {
            computedLoc = {
                latitude: address.latitude,
                longitude: address.longitude,
                searchRadius: userSearchRadius || 10, // Uyj userSearchRadius, jeli dostpne
                locationName: address.city ? (address.country ? `${address.city}, ${address.country}` : address.city) : (address.locationName || '')
            };
            console.log('[UserLocationMap] useMemo: obliczona lokalizacja ze wsprzdnymi:', computedLoc);
        } else {
            // Jeli nie ma wsprzdnych, LocationPicker uyje swoich domylnych (np. Warszawa)
            // ale przekamy searchRadius i pust nazw
            computedLoc = {
                latitude: null,
                longitude: null,
                searchRadius: userSearchRadius || 10,
                locationName: ''
            };
            console.log('[UserLocationMap] useMemo: obliczona lokalizacja z PUSTYMI wsprzdnymi:', computedLoc);
        }
        return computedLoc;
    }, [address, userSearchRadius, hasCoordinates]);

    // Logowanie finalnej wartoci przekazywanej do LocationPicker
    useEffect(() => {
        console.log('[UserLocationMap] Warto initialLocationForPicker PRZED renderowaniem LocationPicker:', initialLocationForPicker);
    }, [initialLocationForPicker]);


    const handleLocationChange = (newLocation) => {
        console.log("[UserLocationMap] LocationPicker zgosi zmian/zapis:", newLocation);
        // Na razie tylko logujemy. Uytkownik manualnie koczy edycj.
        // Jeli LocationPicker miaby callback onSaveSuccess, tutaj moglibymy np. ustawi setIsEditing(false)
        // lub powiadomi komponent nadrzdny (UserProfile) o potrzebie odwieenia danych.
    };

    // Jeli nie ma adresu (ani jego wsprzdnych) i nie jest to profil biecego uytkownika, nic nie pokazuj.
    if (!address && !isCurrentUser) { // Zmieniono warunek, aby sprawdza istnienie 'address'
        console.log('[UserLocationMap] Brak adresu i nie jest to biecy uytkownik. Renderowanie null.');
        return null;
    }

    // Jeli nie ma wsprzdnych (nawet jeli obiekt address istnieje), a jest to biecy uytkownik, poka opcj ustawienia lokalizacji.
    if (!hasCoordinates && isCurrentUser) {
        console.log('[UserLocationMap] Brak wsprzdnych, ale to biecy uytkownik. Pokazywanie opcji ustawienia lokalizacji. isEditing:', isEditing);
        return (
            <div className="mt-3 pt-3 border-top">
                <h5 className="mb-3">
                    <i className="bi bi-map me-2 text-success"></i>
                    Moja Lokalizacja
                </h5>
                {isEditing ? (
                    <>
                        <p className="text-muted mb-3">
                            Ustaw swoj lokalizacj i preferowany promie wyszukiwania.
                            Dziki temu inni uytkownicy bd wiedzieli, gdzie szukasz przedmiotw,
                            a Ty bdziesz widzie oferty z Twojej okolicy.
                        </p>
                        <LocationPicker
                            initialLocation={initialLocationForPicker} // Przekaemy obiekt z null lat/lng, LocationPicker uyje domylnych
                            userId={userId}
                            onLocationChange={handleLocationChange}
                            readOnly={false}
                        />
                        <button
                            className="btn btn-sm btn-outline-secondary mt-3"
                            onClick={() => { console.log("[UserLocationMap] Anulowano dodawanie nowej lokalizacji."); setIsEditing(false);}}
                        >
                            <i className="bi bi-x-circle me-1"></i>
                            Anuluj
                        </button>
                    </>
                ) : (
                    <div className="text-center py-4 border rounded bg-light">
                        <i className="bi bi-geo-alt text-muted fs-1"></i>
                        <p className="mt-2 mb-3">Nie ustawie jeszcze swojej lokalizacji.</p>
                        <button
                            className="btn btn-success"
                            onClick={() => { console.log("[UserLocationMap] Kliknito 'Ustaw Lokalizacj'."); setIsEditing(true); }}
                        >
                            <i className="bi bi-map me-1"></i>
                            Ustaw Lokalizacj
                        </button>
                    </div>
                )}
            </div>
        );
    }

    // Jeli s wsprzdne (lub jest to biecy uytkownik, ktry moe je ustawi, ale zacz od pustego stanu i wszed w edycj)
    // Ten warunek jest teraz obsugiwany przez powysze, wic tu zakadamy, e albo s koordynaty,
    // albo jestemy w trybie edycji dla biecego uytkownika, ktry wanie je ustawia.
    // Jeli `initialLocationForPicker` nie jest `undefined` (co nie powinno si zdarzy), renderuj LocationPicker.
    if (typeof initialLocationForPicker === 'undefined') {
        console.error("[UserLocationMap] KRYTYCZNY BD: initialLocationForPicker jest undefined tu przed renderowaniem LocationPicker!");
        return <div className="alert alert-danger">Bd wewntrzny: Nie mona przygotowa danych mapy.</div>;
    }

    console.log('[UserLocationMap] Renderowanie LocationPicker z initialLocation:', initialLocationForPicker, 'isEditing:', isEditing);
    return (
        <div className="mt-3 pt-3 border-top">
            <div className="d-flex justify-content-between align-items-center mb-3">
                <h5 className="mb-0">
                    <i className="bi bi-map me-2 text-success"></i>
                    {isCurrentUser ? 'Moja Lokalizacja' : 'Lokalizacja'}
                </h5>
                {isCurrentUser && hasCoordinates && ( // Poka przycisk Edytuj tylko jeli s ju jakie koordynaty do edycji
                    <button
                        className="btn btn-sm btn-outline-success"
                        onClick={() => { console.log("[UserLocationMap] Zmieniono tryb edycji na:", !isEditing); setIsEditing(!isEditing);}}
                    >
                        <i className={`bi bi-${isEditing ? 'eye' : 'pencil'} me-1`}></i>
                        {isEditing ? 'Tryb Podgldu' : 'Edytuj'}
                    </button>
                )}
            </div>
            <LocationPicker
                initialLocation={initialLocationForPicker}
                userId={userId}
                onLocationChange={handleLocationChange}
                readOnly={!isEditing && hasCoordinates} // Mapa jest readOnly jeli nie edytujemy ORAZ jeli s koordynaty (czyli nie jestemy w trybie "pierwszego ustawiania")
            />
        </div>
    );
};

export default UserLocationMap;